<template>
    <div class="box">
        <Header :got="true" :unt="true" :gnt="false"></Header>
        <div class="content">
            <div class="hot">
                <ul>
                    <li>
                        <van-search  placeholder="请输入商家或美食名称" v-model="value"/>
                    </li>
                    <li><van-button type="info" @click="search()" class="but"><h4>确认</h4></van-button></li>
                </ul>
            </div>
            <div class="info">
                为了满足商家的送餐要求，建议你从列表中选择地址
            </div>
            <div class="address" v-if="list.length==0">
                <ul>
                    <li>找不到地址？</li>
                    <li>请尝试输入小区，写字楼或学校名</li>
                    <li>详细地址（如门牌号）可稍后输入哦。</li>
                </ul>
            </div>
            <div class="item" v-if="list.length>0">
                <ul v-for="(item,index) of list" :key="index" @click="address(item)">
                    <li>{{item.name}}</li>
                    <li>{{item.address}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import Header from '../header/Header'
    export default {
        data() {
            return {
                value:"",
                list:[]
            }
        },
        components: {
            Header
        },
        methods: {
            search(){
                this.http.get("http://elm.cangdu.org/v1/pois?type=nearby&keyword="+this.value+"").then((res)=>{
                    this.list=res.data
                })
            },
            address(item){
                this.$router.push({path:'/anout/receiv/add', query:{name:item}}) 
                // console.log(item.name)
            }
        }
    }
</script>

<style lang="scss" scoped>
.box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        font-size: 0.35rem;
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        .item{
            width: 100%;
            ul{
                width: 100%;
                height: 1.2rem;
                background: rgb(241, 240, 236);
                font-size: 0.3rem;
                border-bottom: 0.001rem solid rgb(201, 183, 183);
                li{
                   line-height:  0.6rem;
                   margin: 0 0.3rem;
                   color: gray;
                }
            }


        }
        .hot{
            width: 100%;
            height: 1.4rem;
            background: white;
            ul{
                width: 100%;
                display: flex;
                li:nth-of-type(1){
                    flex: 3;
                    margin: 0.3rem 0.2rem;
                }
                li:nth-of-type(2){
                    flex: 1;
                     border-radius: 0.15rem;
                    margin: 0.3rem 0.1rem;
                }
            }
        }
        .info{
            width: 100%;
            height: 0.8rem;
            text-align: center;
            line-height: 0.8rem;
            background: whitesmoke;
            color: green;
            font-size: 0.3rem;
        }
        .address{
            width: 100%;
            text-align: center;
            ul{
                width: 100%;
                padding-top: 50%;
                font-size: 0.3rem;
                color: gray;
            }
        }
    }

</style>


